import axios from 'axios';
import { authHeader } from './authservice/auth-header';

const API_URL = process.env.VUE_APP_BASE_API_URL;

class PaymentService {
    getAllPayment() {
        return axios.get(API_URL+'payment', { headers: authHeader() });
    }
    addPayment(data) {
        return axios.post(API_URL+'payment', data, { headers: authHeader() });
    }
    updatePayment(id, data) {
        return axios.patch(API_URL+'payment/'+id, data, { headers: authHeader() });
    }
    deletePayment(id) {
        return axios.delete(API_URL+'payment/'+id, { headers: authHeader() });
    }
    // bulkdeletePayments(data) {
    //     return axios.post(API_URL +'payment/bulk-delete', data, { headers: authHeader() });
    // }
}

export default new PaymentService();
